export default function CustomizedTabs({ ...props }) {
    const {
        value,
        tabs,
        handleChange,
    } = props;

    const classes = useStyles();
    return (
        <div className={classes.demo}>
            <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
                {tabs.map((label, key) =>
                    <StyledTab key={key} label={label} />
                )}
            </StyledTabs>

        </div>
    );
}


