
import {
    withStyles,
    ListItemIcon,
} from "@material-ui/core";
import PropTypes from 'prop-types';
import listItemStyle from '../../variables/styles/listItemStyle.jsx';
import cx from 'classnames';
import {
   usdFormat,
} from "../../variables/global";

function ListItem({ ...props }) {

    const {
        classes,
        label,
        onClick,
        iconLeft,
        iconRight,
        button,
        header,
        active,
        borderBottom,
        borderTop,
        buttons,
        textRight,
    } = props;


    

    const listItemClass = cx({
        [classes.root]: true,
        [classes.button]: button,
        [classes.header]: header,
        [classes.active]: active,
        [classes.borderTop]: borderTop,
        [classes.borderBottom]: borderBottom,
    });
    // 2.1507137200000000
    // 0.00000001 

    return (
        <div
            button={button}
            onClick={onClick}
            className={listItemClass}
        >

            {iconLeft}
            <div className={classes.label}>{label}</div>
            {iconRight &&
                <ListItemIcon classes={{ root: classes.iconRight }}>
                    {iconRight}
                </ListItemIcon>
            }
            {
                textRight && <div className={classes.labelRight}>{usdFormat(textRight)}</div>
            }
            {buttons.map(item =>
                <div className={classes.buttonStyle}>
                    {item}
                </div>)
            }


        </div>
    );
}

ListItem.propTypes = {
    label: PropTypes.any,
    classes: PropTypes.object.isRequired,
    onClick: PropTypes.func,
    iconLeft: PropTypes.any,
    iconRight: PropTypes.any,
    button: PropTypes.bool,
    active: PropTypes.bool,
    textRight: PropTypes.string,
};

ListItem.defaultProps = {
    buttons: [],
}

export default withStyles(listItemStyle)(ListItem);

