import {
    withStyles,
} from "@material-ui/core";
import PropTypes from 'prop-types';
import listItemStyle from '../../variables/styles/listItemStyle';
import cx from 'classnames';
import { ChevronRight } from "@material-ui/icons";
import { curveMonotoneX } from "d3-shape";
import { ChartCanvas, Chart } from "react-stockcharts";
import { AreaSeries, } from "react-stockcharts/lib/series";
import { discontinuousTimeScaleProvider } from "react-stockcharts/lib/scale";
import { last } from "react-stockcharts/lib/utils";
import {
    primaryColor
} from "../../variables/styles";

function ProductItem({ ...props }) {


    const {
        data: initialData,
        classes,
        label,
        onClick,
        active,
        item,
    } = props;
    const width = window.innerWidth * 0.05;
    const canvasHeight = window.innerHeight * 0.025; // canvas height (vh)
    const lineChartHeight = canvasHeight * 0.60; // area chart height (vh)
    const margin = { left: 1, right: 0, top: 1, bottom: 0 };
    const xScaleProvider = discontinuousTimeScaleProvider.inputDateAccessor(
        (d) => new Date(d[0] * 1000)
    );
    const {
        data,
        xScale,
        xAccessor,
        displayXAccessor,
    } = xScaleProvider(initialData);
    const start = xAccessor(last(data));
    const end = xAccessor(data[Math.max(0, data.length - 150)]);
    const xExtents = [start, end];
    const yExtents = d => d[1];


    const listItemClass = cx({
        [classes.productRoot]: true,
        [classes.active]: active,
    });



    return (
        <div
            // button={true}
            onClick={onClick}
            className={listItemClass}
        >
            <div className={classes.label} >
                {label}
            </div>
            <div className={classes.chart}>
                <ChartCanvas
                    ratio={1}
                    width={width}
                    height={canvasHeight}
                    margin={margin}
                    seriesName="MSFT"
                    data={data}
                    type="canvas"
                    xScale={xScale}
                    xAccessor={xAccessor}
                    displayXAccessor={displayXAccessor}
                    xExtents={xExtents}
                    useCrossHairStyleCursor={false}
                    mouseMoveEvent={false}
                    panEvent={false}
                    zoomEvent={false}
                    clamp={false}
                >
                    <Chart
                        height={lineChartHeight}
                        id={0}
                        yExtents={yExtents}
                    >
                        <AreaSeries
                            yAccessor={yExtents}
                            fill="#00000000"
                            stroke={primaryColor}
                            strokeWidth={2}
                            interpolation={curveMonotoneX}
                        />
                    </Chart>
                </ChartCanvas>

            </div>
            <div className={classes.labelRight}>
                {item.price}
            </div>
            <ChevronRight className={classes.iconRight} />
        </div>
    );
}

ProductItem.propTypes = {
    classes: PropTypes.object.isRequired,
    onClick: PropTypes.func,
    item: PropTypes.any,
};

export default withStyles(listItemStyle)(ProductItem);
