  handleCodeSocketMessage(msg) {
    // receive code response
    let data = JSON.parse(msg.data);
    // console.log("response: ", data);
    // var pythonOutput = data.pythonOutput;
    var pythonError = data.pythonError;
    if (pythonError !== "") {
      console.log("ERROR: ", data.pythonError);
      this.pythonOutput = data.pythonError;
    } else {
      this.codeResponse = data.chart;
      this.newCodeResponse = true;
      this.setState({
        lineColors: data.lineColors
      });
    }
    console.log("CODE RESPONSE: ", data);
  }

  handleCodeSocketClose(msg) {
    console.log(" [ OK ] closing feed websocket");
    //clearTimeout(this.chartTimeout);
    // try to reconnect websocket every 1000 ms
    // this.reconnectFeedInterval = this.reconnectFeedSocket.bind(this);
    // this.reconnectFeedInterval = setInterval(this.reconnectFeedInterval, 1000);
  }

  handleFeedSocketMessage(msg) {
    //var decodedString = new TextDecoder("UTF8").decode(new DataView(msg.data));
    let data = JSON.parse(msg.data);
    this.count = data.count;
    // console.log(data);

    if (this.count > 0) {

      // set USD portfolioValue
      this.portfolioValue = data.portfolioValue;

      // update ticks and matches
      for (var tick of data.tickers) {
        this.tradeHistory.unshift(tick);
        this.ticks.push(tick);
        this.ticks.shift();
        while (this.tradeHistory.length > 40) {
          this.tradeHistory.pop();
        }
      }

      // wait for response from code (dojo)
      if (this.newCodeResponse) {
        this.chart = this.codeResponse;
        this.newCodeResponse = false;
        this.setState({ newCodeResponse: this.newCodeResponse });
      }

      // update the chart
      if (data.ohlcv[0] === this.chart[this.chart.length - 1][0]) {
        // update current candle
        this.chart.pop();
        this.chart.push(data.ohlcv);
      } else {
        // add the next candle
        this.chart.shift();
        this.chart.push(data.ohlcv);
      }
      // reverse asks
      data.book.asks.reverse();
      this.ticker = data.ticker;
      this.book = data.book;
      this.setState({});

    } else {
      this.chart = [...data.candles];
      this.tradeHistory = [...data.snapshot].reverse();
      this.ticks = [...data.snapshot];
    }
  }

  handleFeedSocketClose(msg) {
    console.log(" [ OK ] Closing websocket");
    //clearTimeout(this.chartTimeout);
    // try to reconnect websocket every 1000 ms
    // this.reconnectFeedInterval = this.reconnectFeedSocket.bind(this);
    // this.reconnectFeedInterval = setInterval(this.reconnectFeedInterval, 1000);
  }

