  formatUSD(num) {
    // we round up for USD other than available (quote) USD
    return +(Math.ceil(num + "e+2") + "e-2");
  }

  formatUSDQuote(num) {
    // we round down for our available USD
    return +(Math.floor(num + "e+2") + "e-2");
  }

