import React from "react";
import {
    Button as ButtonMUI,
    withStyles
} from "@material-ui/core";
import PropTypes from 'prop-types';
import buttonStyle from '../../variables/styles/buttonStyle.jsx';
import cx from 'classnames';


function Button({ ...props }) {

    const {
        children,
        classes,
        primary,
        primaryActive,
        dark,
        secondary,
        secondaryActive,
        danger,
        dangerActive,
        plain,
        disabled,
        active,
        padding,
        paddingTop,
        paddingBottom,
        paddingLeft,
        paddingRight,
        ...rest
    } = props;

    const btnClass = cx({
        [classes.primary]: primary && !primaryActive,
        [classes.secondary]: secondary && !secondaryActive,
        [classes.danger]: danger && !dangerActive,
        [classes.primaryActive]: primaryActive,
        [classes.secondaryActive]: secondaryActive,
        [classes.dangerActive]: dangerActive,
        [classes.dark]: dark,
        [classes.disabled]: disabled,
        [classes.plain]: plain,
        [classes.active]: active,
        // padding
        [classes.padding]: padding,
        [classes.paddingTop]: paddingTop,
        [classes.paddingBottom]: paddingBottom,
        [classes.paddingLeft]: paddingLeft,
        [classes.paddingRight]: paddingRight,
    });


    return (
        
        <ButtonMUI
            fullWidth
            className={btnClass}
            {...rest}
        >
            {children}
        </ButtonMUI>

    );
}

Button.propTypes = {
    children: PropTypes.node,
    title: PropTypes.string,
};

export default withStyles(buttonStyle)(Button);





